import {
    navigate
} from 'gatsby';
import React from 'react';
import ajax from './ajax';

class UserData {
    /**
     * @type {string}
     */
    token = null;
    /**
     * @type {import('../../../shared/types/userInfo').UserInfo}
     */
    userInfo = null;

    constructor() {
        if (typeof window !== 'undefined') {
            this.token = localStorage.getItem('token');
        }
    }
    getToken() {
        return new Promise((resolve, reject) => {
            if (this.token)
                resolve(this.token);
            else {
                ajax.post('/auth/login', {
                    "username": localStorage.getItem('user'),
                    "password": localStorage.getItem('pass')
                }).then(x => {
                    save.token = x.data.token;
                    localStorage.setItem('token', x.data.token);
                    resolve(save.token);
                }).catch(x => reject(x));
            }
        });
    }
    getUserInfo() {
        return new Promise((resolve, reject) => {
            if (this.userInfo)
                resolve(this.userInfo);
            else {
                this.getToken().then(() => {
                    return ajax.get('/auth/info');
                }).then(x => {
                    this.userInfo = x.data;
                    resolve(this.userInfo);
                }).catch(x => {
                    reject(x);
                });
            }
        });
    }
}

const save = new UserData();


/**
 * @return {import('../../../shared/types/userInfo').UserInfo}
 */
export const useUserInfo = () => {
    const [data, setData] = React.useState();
    React.useEffect(() => {
        save.getUserInfo().then(x => setData(x)).catch(err => {
            if (err.response) {
                if (err.response.status === 401) {
                    navigate('/user/login/');
                } else {
                    alert(err.response.data.message);
                }
            } else {
                alert(err.message);
            }
        });
    }, [save.userInfo]);
    return data;
}

export default save;