import { CircularProgress, Grid } from "@material-ui/core";
import * as React from "react";

const Defer = ({ forwardRef = undefined, children, data, visible = true }) => {
    return visible ? (data ? children(data) : (
        <Grid container ref={forwardRef} alignItems="center" justifyContent="center">
            <Grid item>
            <CircularProgress />
            </Grid>
        </Grid>
    )) : null;
}

export default Defer;