import axios from 'axios';
import {
    navigate
} from 'gatsby';
import React from 'react';
import save from './save';
import { winalert } from './util';

const ajax = axios.create({
    baseURL: process.env.GATSBY_SERVER_URL,
});

ajax.interceptors.request.use(config => {
    if (save.token) {
        config.headers.common['Authorization'] = 'Bearer ' + save.token;
    }
    return config;
});

const useFetcher = ( /** @type {string} */ url, /** @type {(arg0: any) => void} */ success, /** @type {(arg0: any) => void} */ fails) => {
    const [state, setState] = React.useState(null);
    let disposed = false;
    React.useEffect(() => {
        if (!save.token) {
            navigate('/user/login/');
        }
        if (state)
            setState(null);
        if (!url) {
            return;
        }
        save.getToken().then(x => {
            return ajax.get(url);
        }).then((data) => {
            if (disposed) return;
            setState(data.data);
            if (success)
                success(data.data);
        }).catch(err => {
            setState(null);
            if (err.response) {
                if (err.response.status === 401) {
                    navigate('/user/login/');
                } else if (err.response.status === 404) {
                    winalert('Halaman ini tidak ada').then(
                        () => navigate('/user/')
                    );
                } else if (fails) {
                    err.message = err.response.data.message;
                    fails(err);
                } else {
                    alert(err.response.data.message);
                }
            } else if (fails) {
                fails(err);
            } else {
                if (err.message === "Network Error") {
                    navigate('/offline');
                } else {
                    alert(err.message);
                }
            }
        });
        return () => {
            disposed = true;
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [url]);
    return state;
};
export default ajax;

export {
    useFetcher,
};