const rp = new Intl.NumberFormat('id-ID', {
    style: 'currency',
    currency: 'IDR',
    maximumFractionDigits: 0,
    minimumFractionDigits: 0,
})

export function upcase(string) {
    return string.charAt(0).toUpperCase() + string.slice(1);
}

export const rupiah = (n) => {
    return rp.format(n);
}

export const expandImg = ( /** @type {string} */ url, /** @type {"thumbnail" | "medium" | "small"} */ format) => {
    return process.env.GATSBY_SERVER_URL + '/uploads/' + url + (format ? '?format=' + format : '')
}

export const humanizeTime = ( /** @type {number} */ time) => {

    var now = Date.now();
    var dif = now - time;
    var phrase = "";
    var before = dif > 0;
    var days = Math.trunc(dif / (24 * 3600 * 1000));
    var hours = Math.trunc(dif / (3600 * 1000));
    var minutes = Math.trunc(dif / (60 * 1000));

    if (days !== 0 && (Math.abs(days) >= 7)) {
        phrase = `${Math.ceil(Math.abs(days / 7))} minggu`;
    } else if (days !== 0) {
        // Yesterday/Tomorrow special cases
        if (Math.abs(days) == 1) {
            return before ? "Kemarin" : "Besok";
        }
        phrase = `${Math.abs(days)} hari`;
    } else if (hours !== 0) {
        phrase = `${Math.abs(hours)} jam`;
    } else if (minutes !== 0) {
        phrase = `${Math.abs(minutes)} menit`;
    } else {
        return before ? "Baru saja" : "Sekarang";
    }

    return before ? `${phrase} yang lalu` : `${phrase} lagi`;
}

export const winconfirm = function (msg) {
    return new Promise((resolve, reject) => {
        if (typeof window === 'undefined')
            return resolve(false);
        resolve(window.confirm(msg));
    });
}

export const winprompt = function (msg, def) {
    return new Promise((resolve, reject) => {
        if (typeof window === 'undefined')
            return resolve(false);
        resolve(window.prompt(msg, def));
    });
}

export const winalert = function (msg) {
    return new Promise((resolve, reject) => {
        if (typeof window === 'undefined')
            return resolve(false);
        resolve(window.alert(msg));
    });
}

export const getSearchParam = () => {
    var q = typeof window === 'undefined' ? '' : window.location.search
    return new URLSearchParams(q);
}

export const buildSearchParam = (query) => {
    Object.keys(query).forEach(x => {
        if (query[x] === '') {
            delete query[x];
        }
    });
    return new URLSearchParams(query);
}